footer {
    position: relative;
    bottom: 0px;
    width: 100%;
    background: #2c3e50;
}

.main-content {
    display: flex;
}

.main-content .box {
    flex-basis: 50%;
    padding: 10px 20px;
}

.box h2 {
    font-size: 18px;
    font-weight: 600;
    text-transform: uppercase;
}

.box .content {
    margin: 20px 0 0 0;
    position: relative;
}

.box .content:before {
    position: absolute;
    content: "";
    top: -10px;
    height: 2px;
    width: 100%;
    background: yellow;
}

.box .content:after {
    position: absolute;
    content: "";
    height: 2px;
    width: 15%;
    background: white;
    top: -10px;
}

.center .icons {
    display: flex;
}

.center .icons li {
    position: relative;
    list-style: none;
    height: 50px;
    width: 50px;
    margin: 0 10px;
    border-radius: 50%;
    margin-top: 10px;
    background: #dde1e7;
    cursor: pointer;
    text-align: center;
    transition: 0.5s;
    box-shadow: -3px -3px 7px #ffffff73, 3px 3px 5px #ffffff73;
}

.center .icons li a {
    line-height: 50px;
    font-size: 30px;
    color: #000;
}

.center .icons li:nth-child(1):hover {
    background-color: #3b5998;
}

.center .icons li:nth-child(2):hover {
    background-color: #1da1f2;
}

.center .icons li:nth-child(3):hover {
    background-color: #e1306c;
}

.center .icons li:nth-child(4):hover {
    background-color: #2867b2;
}

.center .icons li:nth-child(5):hover {
    background-color: grey;
}

.center .fas {
    font-size: 24px;
    background: white;
    color: black;
    height: 45px;
    width: 45px;
    line-height: 45px;
    text-align: center;
    border-radius: 50%;
    transition: 0.5s;
    cursor: pointer;
    /* margin-bottom: 10px; */
}

.center .fas:hover {
    background: #f12020;
    color: white;
}

.center .additional {
    margin-top: 56px;
}

.center .text {
    font-size: 17px;
    font-weight: 500;
    padding-left: 10px;
    color: yellow;
}

.center .center-bottom .contact {
    margin: 15px 0;
}

.left .footer-logo {
    text-align: center;
    margin-top: 7px;
}

.left .footer-logo img {
    height: 150px;
    width: 150px;
    border: 4px solid cyan;
    border-radius: 50%;
}

.left h2 {
    margin-top: 0px;
}

.left .content .fas {
    font-size: 24px;
    background: white;
    color: black;
    height: 45px;
    width: 45px;
    line-height: 45px;
    text-align: center;
    border-radius: 50%;
    transition: 0.5s;
    cursor: pointer;
}

.left .content .fas:hover {
    background: #f12020;
    color: white;
}

.left .content .text {
    font-size: 17px;
    font-weight: 500;
    padding-left: 10px;
    color: yellow;
}

.left .content .phone {
    margin: 15px 0;
}

.right form .text {
    font-size: 18px;
    color: white;
}

.right form input,
.right form textarea {
    width: 75%;
    font-size: 15px;
    background: white;
    color: black;
    padding-left: 10px;
    border: 1px solid #222222;
    margin: 10px;
}

.right form input:focus,
.right form textarea:focus {
    outline-color: #3498db;
}

.right form input {
    height: 35px;
}

.right form button {
    display: inline-block;
    background: cyan;
    color: #000;
    font-size: 20px;
    padding: 0 10px;
    margin-top: -5px;
    font-weight: 400;
    border-radius: 6px;
    border: 2px solid cyan;
    transition: all 0.3s ease;
    cursor: pointer;
}

.right form button:hover {
    background: none;
    color: white;
}

.right form button:active {
    background: none;
    color: white;
}

.bottom center {
    padding: 5px;
    font-size: 17px;
    background: #fff;
}

.bottom center span {
    color: #656565;
}

.bottom center a {
    color: #000;
    text-decoration: none;
}

.bottom center a:hover {
    color: green;
    text-decoration: underline;
}

@media screen and (max-width: 900px) {
    footer {
        position: relative;
        bottom: 0px;
    }

    .main-content {
        flex-wrap: wrap;
        flex-direction: column;
    }

    .center .icons li {
        height: 45px;
        width: 45px;
        margin: 0 5px;
        margin-top: 5px;
    }

    .center .icons li a {
        line-height: 45px;
        font-size: 25px;
    }

    .center .icons li.shadow-2 a {
        height: 30px;
        width: 30px;
        line-height: 30px;
        font-size: 22px;
    }

    .left .content .fas {
        font-size: 16px;
        height: 40px;
        width: 40px;
        line-height: 40px;
    }

    .left .content .text {
        padding-left: 3px;
    }

    .left .footer-logo {
        text-align: left;
    }

    .left h2 {
        margin-top: 30px;
    }

    .center .fas {
        font-size: 16px;
        height: 40px;
        width: 40px;
        line-height: 40px;
    }

    .additional {
        margin-top: 50px;
    }
}

@media (max-width: 320px) {
    .bottom *{
        font-size: 12px;
    }

    .center .icons {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-around;
    }
}