@import url(https://fonts.googleapis.com/css2?family=Benne&display=swap);
body {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

::-webkit-scrollbar {
    width: 15px;
}

::-webkit-scrollbar-thumb {
    background: linear-gradient(#2c2b30, #dbd3f0);
    border-radius: 40px;
}


/*---Banner---*/

.banner {
    background-repeat: no-repeat;
    background-size: contain;
    background: url(/FunwithPhysics/funwithphysics/static/media/bg1.30c6b79e.jpg) no-repeat center center fixed;
    background-size: cover;
    height: 100vh;
    width: 100%;
    overflow: hidden;
}
h2 {
    color: white;
    font-family: "Benne", serif;
}

.banner_button {
    padding: 5px;
}


/*---Feature Section---*/

#fh5co-course-categories,
#fh5co-counter,
#fh5co-course,
#fh5co-register,
#fh5co-pricing,
#fh5co-testimonial,
#fh5co-blog,
#fh5co-about,
#fh5co-staff,
#fh5co-contact,
#fh5co-footer {
    padding: 7em 0;
    clear: both;
}

@media screen and (max-width: 768px) {
    #fh5co-course-categories,
    #fh5co-counter,
    #fh5co-course,
    #fh5co-register,
    #fh5co-pricing,
    #fh5co-testimonial,
    #fh5co-blog,
    #fh5co-about,
    #fh5co-staff,
    #fh5co-contact,
    #fh5co-footer {
        padding: 3em 0;
    }
}

.banner {
    padding-left: 5vw;
    padding-top: 30vh;
}

.banner_text {
    margin-bottom: 30px;
}

.banner_button * {
    margin-left: 20px;
    width: 120px;
}

@media (max-width: 600px) {
    .banner {
        padding-top: 25vh;
    }
    .banner h2 {
        font-size: 25px;
    }
}

@media (max-width: 768px) {
    .banner {
        padding-top: 27vh;
    }
    .banner h2 {
        font-size: 30px;
    }
}

@media (max-height: 500px) {
    .banner {
        padding-top: 0vh;
    }
    .banner h2 {
        font-size: 20px;
    }
}

@media (max-width: 320px) {
    .banner {
        padding-top: 25vh;
        height: calc(100vh - 50px);
    }
    .banner h2 {
        font-size: 20px;
    }
    .banner_button * {
        margin-bottom: 20px;
    }
    .gotopbtn {
        left: calc(80vw - 10px);
        top: calc(90vh - 10px);
    }
    #fh5co-course-categories {
        margin: 0px;
        padding: 0px;
    }
}

