*{
    margin: 0;
    padding: 0;
} /* normalizing  */

.mech__main{
    display: grid;
    grid-template-columns: 1fr 4fr 1fr;
    grid-template-rows: auto;
    grid-template-areas: 
    " . header . "
    " . topics .";
}

@media only screen and (max-width:1038px) {
    .mech__main{
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto;
        grid-template-areas: 
        " header . . "
        " . topics .";
    }
}
.mech__header{
    grid-area: "header";
    grid-column-start: 1;
    grid-column-end: span col2-start;
    grid-row-start: auto;
    align-self: stretch;
    justify-self: center;
    margin-top: 1em;
}
.mech__topics-card{
    padding: 3em;
    grid-area: "topics";
    grid-column: 2;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 2em;
    -webkit-column-gap: 2em;
            column-gap: 2em;
    grid-row-gap: 2em;
    row-gap: 2em;
    transition: "opacity 350ms ease";
}
.a:hover{
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
    transition-duration:0.2s ;
    box-shadow: 0 13px 40px -5px hsla(240, 30.1%, 28%, 0.12), 0 8px 32px -8px hsla(0, 0%, 0%, 0.14), 0 -6px 32px -6px hsla(0, 0%, 0%, 0.02);
  
}

.Calculator__main {
    margin: 0em;
    padding: 3em;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "header header" "image details " "formula-text . " "formula formula " " . . " " . . " "process calc ";
    grid-row-gap: 3em;
    row-gap: 3em;
}

.Calculator__image {
    grid-area: image;
}

.Calculator__image img {
    width: 30em;
    height: 20em;
    object-fit: cover;
    box-sizing: border-box;
    border-radius: 1em;
    border: black;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.Calculator__header {
    grid-area: header;
    align-self: center;
}

.Calculator__details {
    grid-area: details;
    align-self: center;
    justify-self: start;
    word-wrap: break-word;
    padding: 1em;
    font-size: 20px;
    width: 30em;
}

.Calculator__formula-text {
    grid-area: formula-text;
    margin-top: 1em;
}

.Calculator__formula {
    grid-area: formula;
    font-family: cursive;
}
.Calculator__dimension {
    grid-area: formula;
    font-family: cursive;
    margin-top: 110px;
}
.Calculator__siunit {
    grid-area: formula;
    font-family: cursive;
    margin-top: 70px;
}

.Calculator__calc {
    grid-area: calc;
    margin-top: -400px;
}

.Calculator__calc hr {
    width: auto;
    border: 1px dashed red;
}

.Calculator__process {
    grid-area: process;
    word-wrap: break-word;
    width: 37.5em;
    margin-top: -100px;
}

